import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'

//
import veiwMain from '@/components/veiwMain'
//
import login from '@/components/login'
//
import indexPage from '@/components/index/indexPage'

//--------------------------------
//
import SiteplanMian from '@/components/managementOfLocation/Siteplan/SiteplanMian'
//
import auditSelectionPage from '@/components/managementOfLocation/auditOfSelection/auditSelectionPage'

//--------------------------------
//
import menuPage from '@/components/system/managementOfMenu/menuPage'
//
import mailListPage from '@/components/system/listOfMail/mailListPage'
//
import userControl from '@/components/system/userControl/userControlPage'


//---------------------------------------
//color
import colorCardPage from '@/components/tplPublic/color'
import iconComponents from '@/components/tplPublic/iconComponents'
import timeLinePage from '@/components/tplPublic/timeLine'
import talkPage from '@/components/tplPublic/talkPage'


//////------------
//////
import newMapPage from '@/components/newMap/newMap'
//
import newmapShow from '@/components/newMap/mapShow'
import mapFloatMain from '@/components/newMap/mapFloatMain'

////

//
import frontEnd_indexPage from '@/components/frontEnd/index/index'
import frontEnd_indexPagecontent from '@/components/frontEnd/index/indexpage'

//
import frontEnd_dataList from '@/components/frontEnd/dataList/dataList'
import frontEnd_dataListPagecontent from '@/components/frontEnd/dataList/dataListPagecontent'
//
import frontEnd_articlePage from '@/components/frontEnd/dataList/content/articlePage'
//-2
import frontEnd_articlePage_two from '@/components/frontEnd/dataList/content/articlePage_two'
//
import frontEnd_timeLine from '@/components/frontEnd/dataList/content/timeLine'
//
import frontEnd_authorizationPage from '@/components/frontEnd/dataList/content/authorizationPage'


//---------------
//user->
import frontEnd_user_mypage from '@/components/frontEnd/user/myPage'
//modifyPassWord->  
import frontEnd_user_modifyPassWord from '@/components/frontEnd/user/modifyPassWord'


//
import frontEnd_user_myFriends from '@/components/frontEnd/user/myFriends'
//
import frontEnd_user_personal from '@/components/frontEnd/user/personal'
// & 
import frontEnd_user_myArticle from '@/components/frontEnd/user/myArticle'
//
import frontEnd_user_privateMessage from '@/components/frontEnd/user/privateMessage'
//
import frontEnd_user_personalContent from '@/components/frontEnd/user/personalContent'


//code_topics




//---------------
//
import frontEnd_user_myProject from '@/components/frontEnd/user/myProject'
import frontEnd_user_myProjectRemarks from '@/components/frontEnd/user/myProjectRemarks'

//-------------------------------------
//mainPage
import creatPage_mainPage from '@/components/creatPage/mainPage'
//defaultPage
import creatPage_defaultPage from '@/components/creatPage/defaultPage'

//api-----------------------
//api->apiListPage
import frontEnd_apiPage_apiListPage from '@/components/frontEnd/apiPage/apiListPage'

//api
//
import frontEnd_apiPage_apicontentPage from '@/components/frontEnd/apiPage/apicontentPage'
//api
import frontEnd_apiPage_debugging from '@/components/frontEnd/apiPage/apidebugging'






//three.js
import three_main from '@/components/threeJS/threeMain'
import three_indexPage from '@/components/threeJS/threeTest'
import three_model from '@/components/threeJS/threeModel'





Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'veiwMain',
      component: veiwMain,
      children: [
        {path: '/',name: 'indexPage',component: indexPage},
        {path: '/index',name: 'indexPage',component: indexPage},
        {path: '/SiteplanMian',name: 'SiteplanMian',component: SiteplanMian},
        {path: '/menuPage',name: 'menuPage',component: menuPage},
        {path: '/mailListPage',name: 'mailListPage',component: mailListPage},
        {path: '/userControl',name: 'userControl',component: userControl},
        {path: '/auditSelectionPage',name: 'auditSelectionPage',component: auditSelectionPage},
        {path: '/colorCardPage',name: '',component: colorCardPage},
        {path: '/iconComponents',name: '',component: iconComponents},
        {path: '/timeLine',name: '',component: timeLinePage},
        {path: '/talkPage',name: '',component: talkPage},
        {path: '/map',name: '',component: newMapPage,children:[
          {path: '/', redirect: '/map/newmapShow'},
          {path: '/map/newmapShow',name: '',component: newmapShow},
          {path: '/map/mapFloatMain',name: '',component: mapFloatMain},
        ]},
        {path: '/apiPage/apicontentPage',name: 'API',component: frontEnd_apiPage_apicontentPage,children:[
             {path: '/', redirect: '/apiPage/apicontentPage/debugging'},
             {path: '/apiPage/apicontentPage/debugging',name: 'API',component: frontEnd_apiPage_debugging},
        ]},

      ]
    },{
      path: '/login',
      name: 'login',
      component: login
    },{
      path: '/frontEndIndexPage',
      name: 'frontEnd_indexPage',
      component: frontEnd_indexPage,
      children: [
        {path: '/',redirect:'/frontEndIndexPage/index'},
        {path: '/frontEndIndexPage/index',name: 'frontEnd_indexPagecontent',component: frontEnd_indexPagecontent},
      ]
    },{
      path: '/frontEnddataList',
      name: 'frontEnddataList',
      component: frontEnd_dataList,
      children: [
        {path: '/',redirect:'/frontEnddataList/index'},
        {path: '/frontEnddataList/index',name: 'frontEnd_dataListPagecontent',component: frontEnd_dataListPagecontent},
        {path: '/frontEnddataList/articlePage',name: 'frontEnd_articlePage',component: frontEnd_articlePage},
        {path: '/frontEnddataList/articlePagetwo',name: 'frontEnd_articlePage_two',component: frontEnd_articlePage_two},
        {path: '/frontEnddataList/timeLine',name: 'frontEnd_timeLine',component: frontEnd_timeLine},
        {path: '/frontEnddataList/authorizationPage',name: 'Authorization page',component: frontEnd_authorizationPage},
        {path: '/frontEnddataList/user',name: 'frontEnd_user_mypage',component: frontEnd_user_mypage,children: [
              {path: '/',redirect:'/frontEnddataList/user/personal'},
              {path: '/frontEnddataList/user/personal',name: '',component: frontEnd_user_personal},
              {path: '/frontEnddataList/user/modifyPassWord',name: '',component: frontEnd_user_modifyPassWord},
              {path: '/frontEnddataList/user/myFriends',name: '',component: frontEnd_user_myFriends},
              {path: '/frontEnddataList/user/myArticle',name: '',component: frontEnd_user_myArticle},
              {path: '/frontEnddataList/user/myFavorites',name: '',component: frontEnd_user_myArticle},
              {path: '/frontEnddataList/user/privateMessage',name: '',component: frontEnd_user_privateMessage},
              {path: '/frontEnddataList/user/personalContent',name: '',component: frontEnd_user_personalContent},

              //
              {path: '/frontEnddataList/user/myProject',name: '',component: frontEnd_user_myProject},
              {path: '/frontEnddataList/user/myProjectRemarks',name: '',component: frontEnd_user_myProjectRemarks},

              {path: '/frontEnddataList/user/apiPageApiListPage',name: 'API',component: frontEnd_apiPage_apiListPage},

        ]},
        
      ]
    },{
      path: '/Hello',
      name: 'Hello',
      component: Hello
    },
    //
    {
       path: '/creatPageMainPage',
       name: 'creatPage_mainPage',
       component: creatPage_mainPage,
       children: [
          {path: '/',redirect:'/creatPageMainPage/defaultPage'},
          {path: '/creatPageMainPage/defaultPage',name: 'creatPage_defaultPage',component: creatPage_defaultPage},
       ]
    },
    {path: '/threemain',name: 'three_main',component: three_main,children: [
        {path: '/threemain/threeindexPage',name: 'three_indexPage',component: three_indexPage},
        {path: '/threemain/threemodel',name: 'three_model',component: three_model},
    ]},
    
  ]
})



