<template>
	
	<div class="personal">


		
		
					<el-form ref="form" :model="form" label-width="80px">


					  <el-form-item class="personal_upImg" label="">
								<el-upload
								  class="avatar-uploader"
								  action="https://jsonplaceholder.typicode.com/posts/"
								  :show-file-list="false"
								  :on-success="handleAvatarSuccess"
								  :before-upload="beforeAvatarUpload">
								  <img v-if="imageUrl" :src="imageUrl" class="avatar">
								  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
								</el-upload>
					  </el-form-item>

					  <el-form-item label="">
					    <el-input v-model="form.name"></el-input>
					  </el-form-item>
					  <el-form-item label="">
					    <el-select v-model="form.region" placeholder="">
					      <el-option label="" value="shanghai"></el-option>
					      <el-option label="" value="beijing"></el-option>
					    </el-select>
					  </el-form-item>
					  <el-form-item label="">
					    <el-col :span="11">
					      <el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
					    </el-col>
					    <el-col class="line" :span="2">-</el-col>
					    <el-col :span="11">
					      <el-time-picker type="fixed-time" placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
					    </el-col>
					  </el-form-item>
					  <el-form-item label="">
					    <el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
					  </el-form-item>
					  <el-form-item label="">
					    <el-checkbox-group v-model="form.type">
					      <el-checkbox label="/" name="type"></el-checkbox>
					      <el-checkbox label="" name="type"></el-checkbox>
					      <el-checkbox label="" name="type"></el-checkbox>
					      <el-checkbox label="" name="type"></el-checkbox>
					    </el-checkbox-group>
					  </el-form-item>
					  <el-form-item label="">
					    <el-radio-group v-model="form.resource">
					      <el-radio label=""></el-radio>
					      <el-radio label=""></el-radio>
					    </el-radio-group>
					  </el-form-item>
					  <el-form-item label="">
					    <el-input type="textarea" v-model="form.desc"></el-input>
					  </el-form-item>
					  <el-form-item label-width="80px">
					    <el-button type="primary" @click="onSubmit"></el-button>
					    <el-button></el-button>
					  </el-form-item>
					</el-form>

	

	</div>

</template>

<script>
  export default {
    data() {
      return {
      	imageUrl: '',
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isJPG && isLt2M;
      },
    }
  }
</script>


