		 	<!-- <el-col :span="24" class="colrIconBtn_div">
		 	 <a class="colrIconBtn" @click="changeLeftNav">
		          <i class="fa fa-arrows-h"></i> <span class="hiddenFont fontSize12"> </span>
		      </a>
		    </el-col> -->
