		 	</el-col> 
		 
		 	<!-- -->
		 	<!-- dom -->

		    <el-col :span="24" v-show="menuChangeShow">
		 		<el-menu :default-active="style.activeIndex" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" theme="dark" :router="style.routerB">
			      <div v-for="item in style.navVal" >
	                <div v-if="item.children.length>0">
	                      <el-submenu v-bind:index="'/'+item.id"  >
	                        <template slot="title">
	                         <i v-bind:class="item.iconCls"></i> <span class="hiddenFont">{{item.text}}</span>
	                        </template>
	                        <el-menu-item-group>
	                            <div v-for="children1 in item.children">
	                              <el-menu-item v-bind:index="'/'+children1.id">
	                                <i v-bind:class="children1.iconCls">&nbsp;</i> {{children1.text}}
	                              </el-menu-item>
	                            </div>
	                        </el-menu-item-group>
	                      </el-submenu>
	                </div>
	                <div  v-else >
	                       <el-menu-item v-bind:index="'/'+item.id" >
	                        	<i v-bind:class="item.iconCls"></i> <span class="hiddenFont">{{item.text}}</span>
	                       </el-menu-item>
	                </div>
	            </div>
			    </el-menu>
		 	</el-col>



		 	<!-- -->

		 	<!-- -->
		 	<!-- dom -->
		 <el-col :span="24" class="tac_iconNav" v-show="!menuChangeShow">
		      <ul>
		          <li v-for="item in style.navVal" class="hasChildLi">
		            <a v-if="item.children.length>0">
		                <i v-bind:class="item.iconCls"></i>
		                <div class="navText hasChildListTit">{{item.text}}
		                    <ul class="childList">
		                        <li v-for="children1 in item.children">
		                            <a  v-on:click="showTitle(children1.id)">
		                              <i v-bind:class="children1.iconCls">&nbsp;</i>{{children1.text}}
		                            </a>
		                        </li>
		                    </ul>
		                </div>
		            </a>
		            <a v-else  v-on:click="showTitle(item.id)" >
		              <i v-bind:class="item.iconCls"></i>
		              <div class="navText">{{item.text}}</div>
		            </a>
		          </li>
		      </ul>
		 </el-col>
		 	<!-- -->


		 </el-row>
	</div>
</template>

