				      <el-checkbox label="" name="type"></el-checkbox>
				    </el-checkbox-group>
				  </el-form-item>
				  <el-form-item label="">
				    <el-radio-group v-model="form.resource">
				      <el-radio label=""></el-radio>
				      <el-radio label=""></el-radio>
				    </el-radio-group>
				  </el-form-item>
				  <el-form-item label="">
				    <el-input type="textarea" v-model="form.desc"></el-input>
				  </el-form-item>
				  <el-form-item>
				    <el-button type="primary" @click="onSubmit"></el-button>
				    <el-button></el-button>
				  </el-form-item>
				</el-form>

		</div>	
	</div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      }
    }
  }
</script>


